;\n        }\n        margin-bottom: 10px;\n    }\n}\n\n.buttons{\n    @include flexs();\n    li{\n        button{\n            @include buttons();\n        }\n    }\n}\n\n\n/*==============-save box=====================*/\n#save{\n    width: 250px;\n    h2{\n        @include valuable-boder(){\n            background: linear-gradient(45deg,rgba(255, 49, 0,0),rgba(255, 49, 0,1),rgba(255, 49, 0,0));\n        }\n        margin-bottom: 30px;\n    }\n    form{\n        label{\n            @include checked_box();\n            @include disabled(check); \n            div{\n                @include checkboxs();\n                margin-bottom: 20px;\n            }\n                \n        }\n    }\n    \n    #directory{\n        #folder{\n            border: 1px solid #c4ad46;\n            color: white;\n            transition-duration: 0.5s;\n            background: rgba(0,0,0,0);\n            cursor: pointer;\n            width: 200px;\n        }\n        \n        #folder:disabled{\n            border: 1px solid #1f1d14;\n            color: #413627;\n            cursor: default;\n        }\n    }\n    \n    \n    .exportImg{\n        @include flexs(space-between,wrap);\n        li{\n            width: 40%;\n            @include checked_box();\n            @include disabled(check);\n            div{\n                @include checkboxs();\n            }\n        }\n    }\n}\n\n\n/*==================compact mode=======================*/\n#compact_mode{\n    height: 400px;\n    width: 230px;\n    display: none;\n    .ext_on_compact{\n        @include flexs();\n        li{\n            @include checkboxs();\n            cursor: default;\n        }\n        .check_on{//onの場合のクラス\n            background: rgba(255,255,255,0.4);\n        }\n    }\n    \n    table{\n        tr{\n            th,td{\n                border: 1px solid white;\n                background: rgba(0,0,0,0);\n            }\n        }\n    }\n    .buttons{\n        @include flexs();\n        button{\n       